.vaccine-app-district {
  position: relative;
  width: 100%;
  margin-top: 0.3rem;
  padding: 0.3rem;
  box-sizing: border-box;
  color: #333;
}

.vaccine-app-district .row-wrapper {
  padding: 0.15rem 0;
  font-size: 0.3rem;
  line-height: 0.5rem;
}

.vaccine-app-district .row-wrapper .picker-list .am-list-body {
  box-sizing: content-box;
  border: 0.01rem solid #e5e5e5;
  border-right: 0.02rem solid #e5e5e5;
  border-left: 0.02rem solid #e5e5e5;
  height: 0.75rem;
  line-height: 0.75rem;
}

.vaccine-app-district .row-wrapper .picker-list .am-list-body .picker-list-item {
  height: 0.75rem;
  line-height: 0.75rem;
}

.vaccine-app-district .row-wrapper .picker-list .am-list-body .picker-list-item .am-list-extra {
  font-size: 0.24rem;
  text-align: left;
}

.vaccine-app-district .row-wrapper .picker-list .am-list-body .picker-list-item .am-list-arrow {
  margin-left: 4rem;
}

.vaccine-app-district .row-wrapper .submit-btn {
  margin: 0 auto;
  width: 2rem;
  height: 0.7rem;
  line-height: 0.7rem;
  border-radius: 0.1rem;
  font-size: 0.34rem;
  background-color: #06cc7e;
  color: #FFF;
}